* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}
html {
  scroll-behavior: smooth;
}
header {
  padding: 30px 45px;
  display: flex;
  justify-content: space-between;
}
h1 {
  font-weight: 300;
  color: white;
}
h2 {
  font-size: 40px;
  font-weight: 500;
}
p {
  font-size: 18px;
  line-height: 24px;
  font-weight: 100;
}
#logo {
  height: 70px;
  vertical-align: middle;
}
#about ul {
  display: flex;
}
#about li {
  padding: 10px 15px;
  margin-left: 50px;
  line-height: 50px;
}
a {
  text-decoration: none;
  color: inherit;
}
li {
  list-style: none;
  color: white;
}

#sb-contact button {
  background: transparent;
  color: white;
  padding: 10px 15px;
  border: 1px solid white;
  margin-left: 100px;
  outline: none;
}
div.parts {
  width: 100%;
  height: 100vh;
  background: linear-gradient(#141417, #151519);
  color: white;
}
#about main {
  width: 40%;
  padding: 20px 50px;
  position: absolute;
  bottom: 30%;
}
#services,
#contact {
  padding: 50px;
}
.servWrapper {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  height: Calc(100% - 90px);
}
.service {
  border: 1px solid white;
  border-radius: 30px;
  padding: 0 20px;
  min-width: 320px;
  width: 31%;
  height: 100%;
  overflow: hidden;
  vertical-align: middle;
  display: flex;
  align-items: center;
}
.servContent {
  margin: auto;
}
#contact main {
  display: flex;
  justify-content: space-between;
}
#contact form {
  width: 300px;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}
#contact input,
#contact button,
#contact textarea {
  width: 100%;
  padding: 12px 15px;
  font-size: 18px;
  font-weight: 100;
  border: none;
  border-radius: 8px;
  display: block;
  margin-top: 20px;
  outline: none;
}
#contact button {
  background: #004dff;
  color: white;
  font-size: 16px;
  cursor: pointer;
}
#contact span {
  font-size: 14px;
  font-weight: 100;
}
#contact ul {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 15%;
}
#contact li {
  padding-right: 80px;
  display: flex;
  font-size: 18px;
  font-weight: 100;
  margin-top: 10px;
}
#contact li img {
  margin-right: 5px;
}
